
import axios from 'axios'
import Card from 'vux/src/components/card/index.vue'

import PullTo from 'vue-pull-to'
export default{
created(){
this.getRandomData()
},
data(){
return {
pageData: [],
}
},
components: {
Card,
PullTo
},
methods: {
getRandomData(loaded){
axios.get('https://gank.io/api/random/data/%E6%8B%93%E5%B1%95%E8%B5%84%E6%BA%90/20')
.then((response) => {
this.pageData = response.data.results
if (loaded) {
loaded('done')
}
})
.catch((error) => {
console.log(error)
if (loaded) {
loaded('fail')
}
})
},
refreshData(loaded){
this.getRandomData(loaded)
}
},
}



