
import axios from 'axios'
import XImg from 'vux/src/components/x-img/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import PullTo from 'vue-pull-to'
export default{
created(){
this.$nextTick(() => {
this.loadPic()
var wrapperHeight = this.$refs.scrollWrapper.clientHeight
this.$refs.scrollContent.style.minHeight = wrapperHeight + 1 + 'px'
})
},
data(){
return {
imgSrc: [],
TOPCONFIG: {
triggerDistance: 50,
stayDistance: 40,
},
pageIndex: 1
}
},
components: {
XImg,
Grid,
GridItem,
PullTo,
InlineLoading,
Previewer,
TransferDom
},
methods: {
show (index) {
this.$refs.previewer.show(index)
},
loadPic(loaded, isMore){
var _this = this
if (isMore) {
_this.pageIndex++
let url = 'https://gank.io/api/data/%E7%A6%8F%E5%88%A9/10/' + _this.pageIndex
axios.get(url)
.then(function (response) {
response.data.results.forEach(function (el, idx) {
_this.imgSrc.push({src: el.url})
})
if (loaded) {
loaded('done')
}
})
.catch(function (error) {
console.log(error)
if (loaded) {
loaded('fail')
}
})
} else {
_this.$nextTick(function () {
axios.get('https://gank.io/api/data/%E7%A6%8F%E5%88%A9/10/1')
.then(function (response) {
_this.imgSrc = []
response.data.results.forEach(function (el, idx) {
_this.imgSrc.push({src: el.url})
})
if (loaded) {
loaded('done')
}
})
.catch(function (error) {
console.log(error)
if (loaded) {
loaded('fail')
}
})
})
}
},
refresh(loaded){
this.loadPic(loaded)
},
loadMore(loaded){
this.loadPic(loaded, true)
}
}
}




